#place_order{
    display: flex;
    padding: 20px 10%;
    flex-wrap: wrap;
    text-transform: capitalize;
    flex-direction: row;
    justify-content:space-between;
    box-sizing: border-box;
    align-items: flex-start;
    h4{
        margin-top: 0;
        margin-bottom: 20px;
        font-size: 1.8em;
    }
   .left-panel{
    padding: 20px;
    box-sizing: border-box;
    box-shadow: lightgrey 0 0 14px 0px;
    margin-right: 5%;
    border-radius: 5px;
    width: 60%;
   }
    .right-panel{
        display: flex;
        flex-wrap: wrap;
        width: 35%;
        align-content: flex-start;
        box-shadow: lightgrey 0 0 14px 0px;
        height: auto;
        border-radius: 5px;
        box-sizing: border-box;
        padding: 20px;
        justify-content: space-between;
    }
    h5{
        font-size: 1.2em;
        font-weight: 500;
        color: indigo;
        margin-top: 0;
        margin-bottom: 5px;
    }
    .main-box{
        box-sizing: border-box;
    }
    .sub-box{
        box-sizing: border-box;
        margin-bottom: 20px;
    }
    .sub-box{
        span, div{
            margin-bottom: 2px;
            margin-left: 20px;
        }
    }
    .flex-full{
        width: 100%;
    }
    .flex-half{
        width: 50%;
    }
    .flex-third{
        width: 33%;
    }
    .flex-fourth{
        width: 25%;
    }
    .flex-quarter{
        width: 75%;
    }
    .flex-6th{
        width: 60%;
    }
    .flex-4th{
        width: 40%;
    }
    a{
        display: flex;
        align-items: center;
        justify-content: center;
        text-decoration: none;
        padding: 10px;
        box-sizing:border-box;
        text-align: center;
        background: indigo;
        color: white;
        border-right: 1px solid white;
        transition: all .3s;
        font-size: 1.2em;
        flex-grow: 1;
        border-bottom: 1px solid white;
        &:hover{
            background: khaki;
            color: indigo;
        }
    }
}