//variables
$border-color: indigo;
$background-input: none;
$background-input-focus: lightyellow;
$background-title: khaki;
$title-font-color: black;
$background-form: none;
$background-button: khaki;
$background-button-hover: indigo;
$font-color-button: black;
$font-color-button-hover: white; 
$background-button-disabled: rgba(240,230,140,.3);
$color-button-disabled: #777777 ;
$half-width: 48%;
$fourth-width: 23%;
//mixins
@mixin css3-prefix($prop, $value) {
    -webkit-#{$prop}: #{$value};
    -moz-#{$prop}: #{$value};
    -ms-#{$prop}: #{$value};
    -o-#{$prop}: #{$value};
    #{$prop}: #{$value};
 }
 //style
.awesome_form{
    &.fix-width.wide{
        width: 800px;
    }
    &.fix-width.medium{
        width: 600px;
    }
    &.fix-width.small{
        width: 400px;
    }
    &.included{
        width: 100%;
        border: none!important;
        box-shadow: none!important;
        padding: 0;
        margin: 0;
    }
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-wrap: wrap;
    width: 50%;
    margin: 5% auto;
    border: 1px solid $border-color;
    padding: 20px; 
    border-radius: 5px;
    
    @include css3-prefix(box-shadow, 2px 2px 8px #888888);
    h5{
        font-weight: normal;
        width: 100%;
        margin: 0;
        text-align: center;
        color: $title-font-color;
        font-size: 2em;
        padding: 5px;
        border-radius: 3px 3px 0 0;
        margin-bottom: 5px;

    }
    .aform-field{
        display: flex;
        flex-wrap: wrap;
        width: 100%;
        margin-bottom: 10px;
        transition: all 0.3s;
        height: auto;
        align-items: center;
        font-family: inherit;
        &.half{
            width: $half-width!important;
        }
        &.fourth{
            width: $fourth-width!important;
        }
        &.hidden-field{
            display: none;
            height: 0;
        }
        label{
            font-size: 0.9em;
            width: auto;;
        }
        select{
            width: 100%;
            padding: 10px;
            font-style:inherit;
            clear: both;
            border: 1px solid indigo;
            &:focus{
                outline: none;
                box-shadow: indigo 0 0 2px 1px;
                background: lightyellow;
            }
            font-family: inherit;
        }
        input[type='text'],input[type='email'],input[type='password']{
            padding: 10px;
            border: 1px solid $border-color;
            width: 100%;
            transition: background-color 0.5s;
            font-family: inherit;
            &:focus{
                background: $background-input-focus; 
                outline: none;
                @include css3-prefix(box-shadow, 0px 0px 3px #800080)
            }
            &::selection {
                background: indigo;
                color: white;
            }
            &::-moz-selection {
                background: indigo;
                color: white;
            }
        }
       input[type=text]:disabled{
           background: lighten(indigo,60);
           border: none;
       }
       input[type=number]{
           font-family: inherit;
           padding: 10px;
           border: 1px solid indigo;
           &:first-child{
             margin-left: 10px;  
           }
           &.one-digit{
               width: 25px;
           }
           &.two-digits{
               width: 40px;
           }
           &.four-digits{
               width: 50px;
           }
           margin-left: 10px;
           &:focus{
            outline: none;
            box-shadow: indigo 0 0 4px 1px;
            background: lightyellow;
           }
       }
       textarea{
           height: 150px;
           width: 100%;
           transition: all 0.2s;
           border: 1px solid $border-color;
           padding: 10px;
           &:focus{
               background-color: $background-input-focus;
               outline: none;
               @include css3-prefix(box-shadow, 0px 0px 3px #800080)
           }
       }
    }
    .error{
        font-size: 0.8em;
        color: crimson;
    }
    button.aform-button
    {
        &.hidden-btn{
            display:none;
        }
        &.submit, input[type=submit]{
            background: $background-button;
            color:$font-color-button;
            padding: 10px;
            margin: auto;
            &.half{
                width: $half-width;
            }
            &.fourth{
                width: $fourth-width;
            }
            &:hover{
                background: $background-button-hover;
                color: $font-color-button-hover
            }
            &.disabled{
                background-color: $background-button-disabled ;
                color: $color-button-disabled;
                transition: none;
                &:hover{
                    background-color: $background-button-disabled;
                    color: $color-button-disabled;
                    cursor: default;
                }
            }
        }
    }
    .error-message{
        color: firebrick;
        font-weight: 500;
        display: flex;
        width: 100%;
        align-items: center;
        justify-content: center;
        padding: 5px;
    }
    .hidden-message{
        display:none;
    }
}